        this.sel2 = true;
        this.sel3 = false;
        this.sel4 = false;
      } else if(num === 3) {
        $("#roadmap-title").html("Phase 3");
        $("#roadmap-text").html("There will be a Gen2 of the EggHead NFT, but this time in 3D. We want to reward everyone who has an EggHead NFT Gen1 with a Gen2. The EggHead token should already be purchasable, listed and have value by this time. ");
